@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body{margin:0;padding:0;height:100vh;font-family:'Varela Round', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

#landing-page{height:3000px;background:#d9a7c7;background:linear-gradient(to top, #fffcdc, #d9a7c7);overflow:hidden}.particle-background{position:absolute;margin-top:-10px;width:100vw;height:150vh}@media screen and (max-width: 910px){.particle-background{height:100vh}}

#nav-container{display:flex;flex-direction:row;justify-content:space-between}#logo-left{height:160px;display:flex;align-items:flex-end;width:20vw;margin-left:75px}#logo-left a{margin:0;padding:0;text-transform:none;text-decoration:none}#logo-left a img{margin:0;padding:0;width:50px;height:auto;padding:10px;border:2px solid white;border-radius:50%;margin-bottom:10px}#logo-left h2{margin:0;padding:0;font-size:2rem;font-weight:400}#logo-left h2 span{color:red}#rightside-container{height:160px;display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-end}.profile-container{display:flex;flex-direction:row}.profile-container h4{margin:0;padding:0;margin-right:25px;margin-top:3px;letter-spacing:2px;color:white}.profile-bar{margin:0;padding:0;width:350px;height:25px;margin-bottom:25px;margin-right:75px;border-radius:15px;background:#6fade3;background:linear-gradient(90deg, rgba(111,173,227,0.500619) 0%, #6fade3 70%, #fff 70%)}.navlink-container{margin:0;padding:0;margin-bottom:25px;margin-right:75px}@media screen and (max-width: 910px){#nav-container,#logo-left,.navlink-container{height:100px}}.navlink-container a{position:relative;font-size:1rem;font-weight:500;color:white;text-decoration:none;cursor:pointer;z-index:0;padding:5px 15px;border:1.5px solid white;border-radius:30px;margin-left:52.5px}.navlink-container a:before,.navlink-container a:after{position:absolute;left:50%;top:50%;width:40px;height:40px;border:4px solid #FFF9E3;transform:translateX(-50%) translateY(-50%) scale(0.8);border-radius:50%;background:transparent;content:"";opacity:0;transition:all 0.3s;z-index:-1}.navlink-container a:after{border-width:2px;transition:all 0.4s}.navlink-container a:hover:before{opacity:0.7;transform:translateX(-50%) translateY(-50%) scale(1)}.navlink-container a:hover:after{opacity:0.7;transform:translateX(-50%) translateY(-50%) scale(1.3)}

.info-carousel{margin-bottom:50px;width:80vw;height:40vw;border-radius:30px;box-shadow:rgba(17,12,46,0.15) 0px 48px 100px 0px}@media screen and (max-width: 910px){.info-carousel{width:90vw;height:90vw}}#infocarousel-container{margin-top:50px}#ic-description-container h1{margin:0;padding:0;margin-left:1vw;margin-top:50px;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}#ic-description-container{width:80vw;left:0;right:0;margin:0 auto}img{height:100vh;width:auto}@media screen and (max-width: 910px){.info-carousel{width:90vw;height:65vh}img{width:auto;height:100vh}}.swiper-button-prev{color:white;left:0;padding-left:25px}.swiper-button-next{color:white;right:0;padding-right:25px}

#dashboard-container{left:0;right:0;margin:auto;margin-top:100px;width:80vw;height:auto}#dashboard-container h1{margin:0;padding:0;margin-left:1vw;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}@media screen and (max-width: 910px){#dashboard-container{width:90vw}}

#chart {
    display: grid;
    place-items: center;
    width: 35vw;
    height: 25vw;
    background-color: rgb(248, 248, 248);
    border-radius: 30px;
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}
@media screen and (max-width: 910px) {
    #chart {
        width: 100%;
        min-height: 300px;
    }
}
